.card {
    width: 485.41px;
    height: 300px;
    background: #ebebeb;
    border-radius: 5px;
    display: flex;
    align-items: center;
    z-index: 10;
}

.img-container {
    height: calc(100% - 36px);
    margin: 16px;
    position: relative;
    display: flex;
    align-items: center;
    border: 2px solid black;
}

.avatar {
    height: 70%;
}

.card-text {}

.card-text>div:first-child {
    font-size: 42px;
    font-weight: bold;
}

.card-text>div:last-child {
    font-size: 28px;
    font-weight: lighter;
    margin: 16px 0;
}

.message {
    font-family: 'Codystar';
    font-size: 44px;
    color: white;
